.NavigationItem {
    margin: 10px 10px;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItem a {
    
    color: black;
    font-weight: 900;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    color: #40a4c8;
}

/* For desktop */
@media(min-width: 500px){
    .NavigationItem {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }

    .NavigationItem a {
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }

    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        background-color: #0b0438;
        border-bottom: 4px solid #40a4c8;
        color: white;
    }
}